.eca-list-group {
  width           : 100%;
  /* padding-right: 5px; */
  border-right    : 1px solid #000000;
  height          : 100%;
}

.eca-list-item {
  position     : relative;
  margin-bottom: 10px;
  padding      : 10px 6px;
  cursor       : pointer;
}

.is-active {
  background-color: rgba(240, 188, 140, 0.473);
}

.is-active::before {
  content         : '';
  position        : absolute;
  top             : 0;
  left            : 0;
  height          : 100%;
  width           : 4px;
  background-color: rgb(248, 165, 56);
}

.is-active-list {
  background-color: rgba(77, 224, 77, 0.377);
}

.is-active-list::before {
  content         : '';
  position        : absolute;
  top             : 0;
  left            : 0;
  height          : 100%;
  width           : 4px;
  background-color: rgb(26, 131, 5);
}