body {
  min-height: 100%;
  width     : 100%;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.align-center {
  align-items: center;
}

.box-content {
  width: 100%;
}

.line {
  width           : 100%;
  height          : 1px;
  background-color: #000;
}

.content {
  width        : 100%;
  line-height  : 40px;
  height       : 40px;
  padding      : 0 14px;
  border-bottom: 1px solid #918d8d;
}